* {
	box-sizing: border-box;
}

.button-wrapper {
	a {
		text-decoration: none !important;
		cursor: auto;
	}
	&.vchat-editor {
		max-width: 840px;
		margin: 0 auto;
	}
}

.wp-block-create-block-vchat-block {
	&.vc-desktop-only {
		display: inline-flex;
	}
	&.vc-tablet-only {
		display: inline-flex;
	}
	&.vc-mobile-only {
		display: inline-flex;
	}
	&.vc-mobile-tablet-only {
		display: inline-flex;
	}
}
